import React from "react";
import { Outlet } from "react-router-dom";

import appRoutes from "constants/appRoutes";
import Tools from "components/Tools";
import { getLastPart } from "helpers/string";
import forexIcon from "images/currency-ex.svg";

export default function Money() {
  return (
    <>
      <Tools listMap={toolsListMap} />
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

const toolsListMap = new Map([
  [
    appRoutes.toolsMoneyForex.path,
    {
      name: getLastPart(appRoutes.toolsMoneyForex.path, "/"),
      icon: forexIcon,
      description: appRoutes.toolsMoneyForex.title
    }
  ]
]);
